import React from 'react';

import classes from './Main.module.css';

const stages = [
    {
        phase: '01',
        title: ' ',
        desc: '    \'    .    .'
    },
    {
        phase: '02',
        title: '',
        desc: '         .        .'
    },
    {
        phase: '03',
        title: ', ,   ',
        desc: ' ,  ,      .    ,     .'
    }
]

const Main = (props) => {
    return (
        <main>
            <section className={classes.Process}>
                <div className={classes.Wrapper}>
                    <div className={classes.Header}>
                        <span>  </span>
                        <h2> </h2>
                        <p>   <a href="/#">+38 (097) 769 97 99 </a>
                             </p>
                    </div>
                    <div className={classes.Phase}>
                        {stages.map(stage =>
                            <div key={stage.phase}>
                                <div>{stage.phase}</div>
                                <h3>{stage.title}</h3>
                                <p>{stage.desc}</p>
                            </div>
                        )}
                    </div>
                </div>
            </section>
            <section className={classes.About}>
                <div className={classes.Wrapper}>
                    <span> </span>
                    <h2> ?</h2>
                </div>
            </section>
        </main>
    )
}

export default Main;
